body {
  font-family: 'Roboto', 'Arial', sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: $brown;
  background-color: $bg;
  min-width: 380px;
  padding: 0;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
