body {
  font-family: "Roboto", "Arial", sans-serif;
  font-size: 14px;
  line-height: 17px;
  color: #3c3c3c;
  background-color: #f8ffff;
  min-width: 380px;
  padding: 0;
  margin: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  width: 330px;
  padding: 0 25px;
  margin: 0 auto;
}
@media (min-width: 720px) {
  .container {
    width: 630px;
    padding: 0 45px;
  }
}

.main-header {
  position: relative;
  background: url("../img/mobile/bg-header.svg") no-repeat;
  background-position: 0 315px;
  background-size: contain;
}

.main-header__intro-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 63px;
  padding-bottom: 170px;
}

.main-header__title {
  font-size: 32px;
  line-height: 40px;
  text-align: center;
  text-transform: uppercase;
  color: #1e959b;
  margin: 0;
}

.main-header__description {
  font-weight: 300;
  margin-top: 20px;
  margin-bottom: 150px;
}

.main-header__new-project {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 232px;
  height: 70px;
  background-color: #f05670;
  border-radius: 20px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.main-header__new-project:hover, .main-header__new-project:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

.main-header__partners {
  position: absolute;
  top: 635px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 340px;
  height: 60px;
  background-color: #1e959b;
  padding: 15px 20px;
  opacity: 0.9;
}

.main-header__partners-list {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 77%;
  margin: 0;
  padding: 0;
  list-style: none;
  transition: all ease 0.5s;
}

.main-header__partners-item {
  width: 260px;
  height: 54px;
  background-size: contain;
  background-position: center;
}
.main-header__partners-item:not(:first-of-type) {
  display: none;
}
.main-header__partners-item:first-of-type {
  background-image: url("../img/partners/wooter.svg");
  background-repeat: no-repeat;
}
.main-header__partners-item:nth-of-type(2) {
  background-image: url("../img/partners/smartbar.svg");
  background-repeat: no-repeat;
}
.main-header__partners-item:nth-of-type(3) {
  background-image: url("../img/partners/cabify.svg");
  background-repeat: no-repeat;
}
.main-header__partners-item:nth-of-type(4) {
  background-image: url("../img/partners/kari.svg");
  background-repeat: no-repeat;
}
.main-header__partners-item:last-of-type {
  background-image: url("../img/partners/uralairlines.svg");
  background-repeat: no-repeat;
}

.main-header__button {
  width: 17px;
  height: 22px;
  background: transparent url("../img/mobile/arrow.svg") no-repeat;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.main-header__button--previus {
  transform: rotate(180deg);
}

@media (min-width: 720px) {
  .main-header {
    background: url("../img/tablet/bg-header.svg") no-repeat;
    background-size: 400px 400px;
    background-position: 275px 130px;
  }

  .main-header__intro-block {
    align-items: flex-start;
    width: 320px;
    padding-top: 80px;
    padding-bottom: 100px;
  }

  .main-header__title {
    font-size: 31px;
    text-align: left;
    width: 222px;
  }

  .main-header__description {
    margin-bottom: 90px;
  }

  .main-header__partners {
    position: static;
  }

  .main-header__partners-list {
    width: 100%;
    justify-content: space-between;
  }

  .main-header__partners-item {
    display: block;
    width: 120px;
    height: 45px;
  }
  .main-header__partners-item:not(.main-header__partners-list) {
    display: block;
  }

  .main-header__button {
    display: none;
  }
}
.main-nav {
  padding-top: 44px;
}

.main-nav__logo {
  display: block;
  width: 318px;
  height: 77px;
  background: url("../img/logo.svg") no-repeat;
  background-size: cover;
  transition: 0.5s;
}
.main-nav__logo:hover, .main-nav__logo:focus {
  opacity: 0.8;
  transition: 0.5s;
}

.main-nav__link-block {
  display: none;
}

@media (min-width: 720px) {
  .main-nav {
    display: flex;
    justify-content: space-between;
  }

  .main-nav__logo {
    width: 256px;
    height: 62px;
  }

  .main-nav__link-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 260px;
  }

  .main-nav__link {
    font-weight: 700;
    text-decoration: none;
    color: #1e959b;
    display: inline-block;
  }
  .main-nav__link--telegram {
    width: 35px;
    height: 25px;
    background: url("../img/telegram.svg") no-repeat;
    background-size: cover;
  }
  .main-nav__link--mail {
    width: 35px;
    height: 25px;
    background: url("../img/mail.svg") no-repeat;
    background-size: cover;
  }
}
.about {
  padding-top: 33px;
}

.about__preferences-list {
  display: none;
}

.about__info-block {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about__info-block::before {
  content: "";
  position: absolute;
  top: 230px;
  width: 314px;
  height: 330px;
  background-image: url("../img/mobile/about.png");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-resolution: 2dppx) {
  .about__info-block::before {
    background-image: url("../img/mobile/about@2x.png");
    background-repeat: no-repeat;
    background-size: contain;
  }
}

.about__title {
  font-size: 29px;
  line-height: 38px;
  text-transform: uppercase;
  text-align: center;
  color: #1e959b;
  margin: 0;
}

.about__description {
  font-weight: 300;
  margin-top: 27px;
  margin-bottom: 0;
}

.about__link-interview {
  letter-spacing: 1px;
  text-transform: uppercase;
  font-weight: 700;
  text-decoration: none;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 257px;
  height: 70px;
  background-color: #f05670;
  margin-top: 368px;
  border-radius: 20px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.about__link-interview:hover, .about__link-interview:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

@media (min-width: 720px) {
  .about {
    padding-top: 70px;
  }

  .about__preferences-list {
    display: flex;
    justify-content: space-between;
    width: auto;
    height: 130px;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  .about__preferences-item {
    position: relative;
    font-size: 15px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 120px;
    height: 100%;
    background-image: linear-gradient(180deg, #FFFFFF 0%, #F7F9FD 100%);
    border-radius: 25px;
    transition: 0.5s;
  }
  .about__preferences-item:hover {
    transform: scale(1.1);
    transition: 0.5s;
  }
  .about__preferences-item::before {
    content: "";
    position: absolute;
    top: 15px;
    width: 50px;
    height: 50px;
    background-repeat: no-repeat;
    background-size: contain;
  }
  .about__preferences-item:first-of-type::before {
    background-image: url("../img/preferences/interview.svg");
  }
  .about__preferences-item:nth-of-type(2)::before {
    background-image: url("../img/preferences/plan.svg");
  }
  .about__preferences-item:nth-of-type(3)::before {
    background-image: url("../img/preferences/comand.svg");
  }
  .about__preferences-item:nth-of-type(4)::before {
    background-image: url("../img/preferences/develop.svg");
  }
  .about__preferences-item:last-of-type::before {
    background-image: url("../img/preferences/metrics.svg");
  }
  .about__preferences-item p {
    font-weight: 300;
    text-transform: uppercase;
    margin: 0 0 19px 0;
  }

  .about__info-block {
    align-items: flex-start;
    width: 295px;
    margin-top: 60px;
    margin-left: auto;
  }
  .about__info-block::before {
    top: 0;
    left: -340px;
  }

  .about__title {
    font-size: 22px;
    line-height: 29px;
    text-align: left;
  }

  .about__link-interview {
    margin-top: 44px;
  }
}
.projects {
  padding-top: 90px;
}

.projects__intro {
  font-size: 29px;
  line-height: 38px;
  text-transform: uppercase;
  text-align: center;
  color: #1e959b;
  margin: 0;
}

.projects__list {
  display: flex;
  flex-direction: column;
  row-gap: 25px;
  margin: 29px 0 0 0;
  padding: 0;
  list-style: none;
}

.projects__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 25px;
  background: linear-gradient(180deg, #FFFFFF 0%, #F7F9FD 100%);
  padding-bottom: 25px;
  padding-left: 45px;
  padding-right: 45px;
  box-shadow: 0px 3px 4px rgba(30, 149, 155, 0.5);
}
.projects__item:nth-of-type(4), .projects__item:nth-of-type(5), .projects__item:nth-of-type(6) {
  display: none;
}

.projects__title {
  font-size: 16px;
  line-height: 20px;
  margin-top: 19px;
  margin-bottom: 5px;
}
.projects__title a {
  color: #1e959b;
  text-decoration: none;
  display: inline-block;
  transition: 0.5s;
}
.projects__title a:hover, .projects__title a:focus {
  transform: scale(1.2);
  opacity: 0.7;
  transition: 0.5s;
}

.projects__description {
  font-weight: 300;
  margin: 0;
}

.projects__all-projects {
  font-size: 16px;
  line-height: 20px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  color: #1e959b;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 233px;
  height: 70px;
  margin: 30px auto 0 auto;
  border: 1px solid #1e959b;
  border-radius: 20px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.projects__all-projects:hover, .projects__all-projects:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

@media (min-width: 720px) {
  .projects__intro {
    font-size: 25px;
    line-height: 30px;
  }

  .projects__list {
    display: grid;
    grid-template-columns: min-content min-content;
    justify-content: space-between;
    margin-top: 35px;
  }

  .projects__item {
    width: 210px;
  }
  .projects__item:nth-of-type(4) {
    display: flex;
  }
}
.order {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 100px;
  padding-left: 25px;
  padding-right: 25px;
}
.order::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 110px;
  background-color: #1e959b;
}

.order__list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.order__item {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
  width: 248px;
  height: 345px;
  background: linear-gradient(180deg, #FFFFFF 0%, #F7F9FD 100%);
  border-radius: 30px;
}
.order__item:not(:first-of-type) {
  display: none;
}
.order__item::before {
  content: "";
  position: absolute;
  top: -60px;
  width: 170px;
  height: 170px;
  background-color: #1e959b;
  border-radius: 50%;
}
.order__item h4 {
  font-size: 24px;
  line-height: 30px;
  text-transform: uppercase;
  color: #1e959b;
  margin: 0;
}
.order__item p {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 1px;
  margin-top: 15px;
  margin-bottom: 32px;
}
.order__item a {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f05670;
  width: 207px;
  height: 62px;
  border-radius: 20px;
  margin-bottom: 22px;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.order__item a:hover, .order__item a:focus {
  transform: scale(1.05);
  transition: 0.5s;
}
.order__item:first-of-type::before {
  background-image: url("../img/ecom.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.order__item:nth-of-type(2)::before {
  background-image: url("../img/saas.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.order__item:nth-of-type(3)::before {
  background-image: url("../img/mob-app.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.order__button {
  width: 17px;
  height: 22px;
  background: transparent url("../img/mobile/arrow.svg") no-repeat;
  margin: 0;
  padding: 0;
  border: none;
  cursor: pointer;
}
.order__button--previus {
  transform: rotate(180deg);
}
.order__button--next {
  transform: rotate(0);
}

@media (min-width: 720px) {
  .order {
    padding-left: 45px;
    padding-right: 45px;
  }

  .order__list {
    width: 100%;
    justify-content: space-around;
  }

  .order__item {
    width: 200px;
    height: 300px;
  }
  .order__item:not(.order__list) {
    display: flex;
  }
  .order__item::before {
    width: 135px;
    height: 135px;
  }
  .order__item h4 {
    font-size: 20px;
  }
  .order__item a {
    width: 180px;
  }

  .order__button {
    display: none;
  }
}
.application {
  position: relative;
  padding: 70px 25px 0 25px;
}
.application::before {
  content: "";
  position: absolute;
  width: 343px;
  height: 345px;
  background-image: url("../img/application.svg");
  background-repeat: no-repeat;
  background-position: 10px center;
}

.application__link {
  font-size: 20px;
  line-height: 26px;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f05670;
  width: 300px;
  height: 80px;
  border-radius: 20px;
  margin: 405px auto 0 auto;
  box-shadow: 0px 5px 10px rgba(30, 149, 155, 0.5);
  transition: 0.5s;
}
.application__link:hover, .application__link:focus {
  transform: scale(1.05);
  transition: 0.5s;
}

@media (min-width: 720px) {
  .application {
    padding: 150px 45px;
  }
  .application::before {
    top: 40px;
  }

  .application__link {
    width: 270px;
    height: 60px;
    margin: 60px 0 0 auto;
  }
}
.main-footer__all-info {
  padding-top: 90px;
  padding-bottom: 30px;
}

.main-footer__link-block {
  display: flex;
  justify-content: space-between;
}
.main-footer__link-block a {
  font-size: 15px;
  line-height: 19px;
  letter-spacing: 1px;
  font-weight: 300;
  text-decoration: none;
  color: #1e959b;
}

.main-footer__contact-block {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 35px;
}

.main-footer__contact-us {
  font-weight: 700;
  text-align: center;
  color: #1e959b;
  flex-basis: 100%;
  margin-bottom: 35px;
}

.main-footer__link {
  display: block;
  font-weight: 700;
  text-decoration: none;
  color: #1e959b;
}
.main-footer__link--phone {
  position: relative;
  margin-left: 34px;
}
.main-footer__link--phone::before {
  content: "";
  position: absolute;
  top: -7px;
  left: -34px;
  width: 18px;
  height: 32px;
  background: url("../img/phone.svg") no-repeat;
}
.main-footer__link--telegram {
  width: 46px;
  height: 35px;
  background: url("../img/telegram.svg") no-repeat;
  margin-left: auto;
  margin-right: 31px;
}
.main-footer__link--mail {
  width: 46px;
  height: 35px;
  background: url("../img/mail.svg") no-repeat;
}

.main-footer__other-info {
  font-size: 12px;
  line-height: 14px;
  position: relative;
  width: 252px;
  margin-top: 35px;
  margin-bottom: 0;
  margin-left: auto;
}
.main-footer__other-info::before {
  content: "";
  position: absolute;
  top: 15px;
  left: -88px;
  width: 80px;
  height: 84px;
  background: url("../img/footer-list.svg") no-repeat;
  background-size: contain;
}

.main-footer__copyright {
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 1px;
  width: max-content;
  margin: 40px auto 0 auto;
  opacity: 0.5;
}

.main-footer__footer-logo {
  display: none;
}

@media (min-width: 720px) {
  .main-footer__all-info {
    display: grid;
    grid-template-rows: repeat(3, min-content);
    padding-top: 80px;
  }

  .main-footer__link-block {
    grid-row: 1;
    grid-column: 1/3;
    justify-content: space-around;
    margin-bottom: 30px;
  }

  .main-footer__contact-us {
    font-size: 18px;
    margin-top: 0;
  }

  .main-footer__link {
    margin-left: 0;
    margin-right: 0;
  }

  .main-footer__contact-block {
    grid-row: 2;
    justify-content: space-evenly;
    width: 340px;
    margin-top: 0;
  }

  .main-footer__other-info {
    font-size: 10px;
    line-height: 12px;
    grid-row: 2;
    margin-top: 0;
  }
  .main-footer__other-info::before {
    display: none;
  }

  .main-footer__copyright {
    grid-row: 3;
    grid-column: 1/3;
    margin-top: 50px;
  }
}